html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

*,
*:focus {
  outline: none;
}

*:focus:not(:focus-visible) {
  outline: none;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  background-color: white;
  color: #1F2937;
}
body.body-fijo {
  overflow: hidden;
}
@media (min-width: 480px) {
  body.body-scroll {
    padding-top: 8.6rem;
  }
}

p {
  font-size: 2rem;
  color: #1F2937;
  line-height: 1.5;
  text-align: justify;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
  color: #111827;
}

h1 {
  font-size: 6.4rem;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
}

h2 {
  font-size: 4.8rem;
  font-weight: 700;
  font-family: "Raleway", sans-serif;
}

h3 {
  font-size: 3rem;
  font-weight: 600;
  font-family: "Raleway", sans-serif;
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
}

ul {
  list-style-type: none; /* Elimina las viñetas */
  padding-left: 0; /* Opcional: elimina el espaciado izquierdo predeterminado */
  margin: 0; /* Opcional: elimina el margen por defecto */
}

.btnPrimary {
  background: linear-gradient(to right, #9181f4, #5038ed);
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.btnPrimary:hover {
  box-shadow: 0 8px 20px rgba(145, 129, 244, 0.4);
}
.btnPrimary:active {
  transform: scale(0.97);
}

.mainLogin {
  display: flex;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0;
}

.sectionShow {
  background-image: url(/src/assets/images/fondoPurpura.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: #FDF6EC;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.showContent {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 45%;
  height: 65%;
  padding: 3rem;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.13);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.16);
}
.showContent h3 {
  text-align: start;
  color: #FFFFFF;
  font-size: 3.8rem;
  font-weight: 700;
}

.iconShow {
  display: flex;
  align-items: end;
  justify-content: end;
}
.iconShow img {
  width: 100%;
  height: 100%;
  max-width: 7rem;
}

.sectionForm {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.formContent {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: start;
  padding-top: 6rem;
  justify-content: center;
}

.mainform {
  height: fit-content;
  display: flex;
  width: 50%;
  flex-direction: column;
  align-items: center;
}

.headerForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 2rem;
}
.headerForm .titleForm {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
.headerForm .spanForm {
  font-size: 1.6rem;
  color: #525252;
}

.formLogin {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
}
.formLogin .formGroup {
  width: 100%;
  margin-bottom: 1.5rem;
}

.divInput {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 0.5rem;
  background-color: #F0EDFF;
  padding: 0 1rem;
}
.divInput input {
  width: 100%;
  padding: 1rem 0.5rem;
  border: none;
  font-size: 1.6rem;
  outline: none;
  background-color: transparent;
  color: gray;
  transition: color 0.3s ease-in-out;
}
.divInput input::placeholder {
  color: gray;
}
.divInput input:focus {
  color: #1F2937;
}
.divInput:focus-within {
  border: 0.2rem solid #9181F4;
  box-shadow: 0 0 0 3px rgba(145, 129, 244, 0.2);
}

.footerForm {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.spanfooter {
  font-size: 1.6rem;
  color: #1F2937;
  font-weight: 500;
  margin-bottom: 1rem;
}

.btnGoogle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem;
  background-color: white;
  color: #1F2937;
  font-size: 1.6rem;
  font-weight: 500;
  border-radius: 0.5rem;
  border: 0.1rem solid #F6EDFF;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  gap: 1rem;
}

.navbarContainer {
  padding: 3rem 4rem;
  width: 100%;
}

.authNavbar {
  display: flex;
  align-items: start;
  gap: 4rem;
}

.nav-item {
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  font-size: 2.4rem;
  color: #1F2937;
  padding-bottom: 0.2rem;
  transition: border-bottom 0.3s ease-in-out;
}

.active {
  border-bottom: 0.2rem solid #8B5CF6;
}

.seccionNavbarhome {
  display: flex;
  justify-content: space-between;
  padding: 2rem 6rem;
}

.cointainerLogo {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.cointainerLogo .textLogo {
  font-size: 2.4rem;
  font-weight: 700;
  color: #000000;
  text-decoration: none;
}

.NavbarHome {
  display: flex;
  align-items: center;
  padding: 0 3rem;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.13);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.16);
}

.navList {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4rem;
}

.navLink {
  text-decoration: none;
  color: #111827;
  font-weight: 500;
}

.activeitem {
  color: #111827;
  font-weight: 700;
  border-bottom: 2px solid #111827;
}

.containerLoginLogUp {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
}

.navLinkLogin {
  font-size: 1.8rem;
  color: #525252;
  text-decoration: none;
  padding: 1.5rem 2.5rem;
}

.navLinkLogUp {
  font-size: 1.8rem;
  color: #FFFFFF;
  background-color: #4E47FF;
  padding: 1.5rem 2.5rem;
  border-radius: 0.6rem;
  text-decoration: none;
}

.sectionInicio {
  display: flex;
  flex-direction: column;
  background-image: url("../assets/images/FondoGradiend.png");
  background-repeat: no-repeat;
  background-size: 120vh;
  background-position: top center;
}

.mainInicio {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15rem;
  height: 70vh;
}

.mainInicio h1 {
  font-size: 6.4rem;
  text-align: center;
  font-weight: bold;
  color: #000000;
}

.navLinkComienzaAhora {
  font-size: 1.8rem;
  margin-top: 2rem;
  background-color: #111827;
  color: white;
  padding: 1.5rem 2.5rem;
  border-radius: 0.6rem;
  text-decoration: none;
}

.sectionCaracteristicas {
  padding: 6rem 20rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section1C {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.containerCaracteristicas {
  display: grid;
  grid-template-columns: 3fr 1fr;
  gap: 2rem;
}

.cointainerSuperior {
  display: flex;
  gap: 2rem;
}

.cointainerInferior {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
}

.caracteristica {
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.13);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.16);
  padding: 2rem;
  height: 100%;
}
.caracteristica h3 {
  margin-bottom: 0;
}
.caracteristica p {
  font-size: 1.6rem;
}

.iconLightContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1.2rem;
  background-color: #4E47FF;
}

.iconContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1.2rem;
  background-color: #4E47FF;
  padding: 1rem;
  width: 4rem;
  height: 4rem;
  margin-bottom: 2rem;
}

.comofuncionaSection {
  padding: 6rem 10rem;
}

.headerComofunciona {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 4rem;
}
.headerComofunciona h2 {
  margin-bottom: 1rem;
}

.carrucelWrapper {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}
.carrucelWrapper .carrucel {
  width: 100%;
}

.carrucelContainer {
  display: flex;
  align-items: center;
  height: 40rem;
  margin: 0 auto;
  max-width: 100rem;
  position: relative;
}